import Axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import UserContext from "../../../../context/UserContext";
import domain from "../../../../util/domain";
import ErrorMessage from "../../../messages/ErrorMessage";

function UpdateFUD(props) {
  const [ma, setMA] = useState();

  const [ffirstname, fsetFirstname] = useState();
  const [flastname, fsetLastname] = useState();
  const [fnickname, fsetNickname] = useState();
  const [fcourseno, fsetCourseno] = useState();
  const [fbirthdate, fsetBirthdate] = useState();
  const [femail, fsetEmail] = useState();
  const [fmainphone, fsetMainphone] = useState();
  const [femergencyphone, fsetEmergencyphone] = useState();
  const [faddresscity, fsetAddresscity] = useState();
  const [faddressline, fsetAddressline] = useState();
  const [frank, fsetRank] = useState();
  const [funit, fsetUnit] = useState();
  const [fsoogHatsava, fsetSoogHatsava] = useState();
  const [fmaslool, fsetMaslool] = useState();

  const [firstname, setFirstname] = useState();
  const [lastname, setLastname] = useState();
  const [nickname, setNickname] = useState();
  const [courseno, setCourseno] = useState();
  const [birthdate, setBirthdate] = useState();
  const [email, setEmail] = useState();
  const [mainphone, setMainphone] = useState();
  const [emergencyphone, setEmergencyphone] = useState();
  const [addresscity, setAddresscity] = useState();
  const [addressline, setAddressline] = useState();
  const [rank, setRank] = useState();
  const [unit, setUnit] = useState();
  const [soogHatsava, setSoogHatsava] = useState();
  const [maslool, setMaslool] = useState();
  const [ready, setReady] = useState(false);

  const { user } = useContext(UserContext);

  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    const getFUD = async () => {
      const FUDRes = await Axios.get(`${domain}/user/getFullDetails`);
      try {
        setMA(FUDRes.data.MA);
      } catch (err) {
        console.log(err);
      }
      try {
        setFirstname(FUDRes.data.FirstName);
      } catch (err) {
        console.log(err);
      }
      try {
        setLastname(FUDRes.data.LastName);
      } catch (err) {
        console.log(err);
      }
      try {
        setNickname(FUDRes.data.NickName);
      } catch (err) {
        console.log(err);
      }
      try {
        setCourseno(FUDRes.data.CourseNo);
      } catch (err) {
        console.log(err);
      }
      try {
        setBirthdate(FUDRes.data.BirthDate.substring(0, 10));
      } catch (err) {
        console.log(err);
      }
      try {
        setEmail(FUDRes.data.Email);
      } catch (err) {
        console.log(err);
      }
      try {
        setMainphone(FUDRes.data.MainPhone);
      } catch (err) {
        console.log(err);
      }
      try {
        setEmergencyphone(FUDRes.data.EmergencyPhone);
      } catch (err) {
        console.log(err);
      }
      try {
        setAddresscity(FUDRes.data.AddressCity);
      } catch (err) {
        console.log(err);
      }
      try {
        setAddressline(FUDRes.data.AddressLine);
      } catch (err) {
        console.log(err);
      }
      try {
        setRank(FUDRes.data.Rank);
      } catch (err) {
        console.log(err);
      }
      try {
        setUnit(FUDRes.data.Unit);
      } catch (err) {
        console.log(err);
      }
      let hatsv;
      try {
        hatsv = FUDRes.data.SoogHatsava === "sadir" ? "" : hatsv;
        hatsv = FUDRes.data.SoogHatsava === "hatsach" ? '"' : hatsv;
        hatsv = FUDRes.data.SoogHatsava === "miluim" ? "" : hatsv;
        setSoogHatsava(hatsv);
      } catch (err) {
        console.log(err);
      }
      let msll;
      try {
        msll = FUDRes.data.Maslool === "mesima" ? "" : msll;
        msll = FUDRes.data.Maslool === "taavura" ? "" : msll;
        msll = FUDRes.data.Maslool === "versatili" ? "" : msll;
        setMaslool(msll);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetFirstname(FUDRes.data.FirstName);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetLastname(FUDRes.data.LastName);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetNickname(FUDRes.data.NickName);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetCourseno(FUDRes.data.CourseNo);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetBirthdate(FUDRes.data.BirthDate.substring(0, 10));
      } catch (err) {
        console.log(err);
      }
      try {
        fsetEmail(FUDRes.data.Email);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetMainphone(FUDRes.data.MainPhone);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetEmergencyphone(FUDRes.data.EmergencyPhone);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetAddresscity(FUDRes.data.AddressCity);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetAddressline(FUDRes.data.AddressLine);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetRank(FUDRes.data.Rank);
      } catch (err) {
        console.log(err);
      }
      try {
        fsetUnit(FUDRes.data.Unit);
      } catch (err) {
        console.log(err);
      }
      let fhatsv;
      try {
        fhatsv = FUDRes.data.SoogHatsava === "sadir" ? "" : fhatsv;
        fhatsv = FUDRes.data.SoogHatsava === "hatsach" ? '"' : fhatsv;
        fhatsv = FUDRes.data.SoogHatsava === "miluim" ? "" : fhatsv;
        fsetSoogHatsava(fhatsv);
      } catch (err) {
        console.log(err);
      }
      let fmsll;
      try {
        fmsll = FUDRes.data.Maslool === "mesima" ? "" : fmsll;
        fmsll = FUDRes.data.Maslool === "taavura" ? "" : fmsll;
        fmsll = FUDRes.data.Maslool === "versatili" ? "" : fmsll;
        fsetMaslool(msll);
      } catch (err) {
        console.log(err);
      }
      setReady(true);
    };
    getFUD();
  }, []);

  async function updatefud(e) {
    e.preventDefault();
    let hatsv2;
    try {
      hatsv2 = fsoogHatsava === "" ? "sadir" : hatsv2;
      hatsv2 = fsoogHatsava === '"' ? "hatsach" : hatsv2;
      hatsv2 = fsoogHatsava === "" ? "miluim" : hatsv2;
    } catch (err) {
      console.log(err);
    }
    let msll2;
    try {
      msll2 = fmaslool === "" ? "mesima" : msll2;
      msll2 = fmaslool === "" ? "taavura" : msll2;
      msll2 = fmaslool === "" ? "versatili" : msll2;
    } catch (err) {
      console.log(err);
    }

    const updateFUDData = {
      firstname: ffirstname,
      lastname: flastname,
      nickname: fnickname,
      courseno: fcourseno,
      birthdate: fbirthdate,
      email: femail,
      mainphone: fmainphone,
      emergencyphone: femergencyphone,
      addresscity: faddresscity,
      addressline: faddressline,
      rank: frank,
      unit: funit,
      soogHatsava: hatsv2,
      maslool: msll2,
    };

    try {
      await Axios.put(`${domain}/user/updateFullDetails`, updateFUDData);
      props.suc("  !");
      const done = props.whendone;
      done(false);
    } catch (err) {
      if (err.response) {
        if (err.response.data.errorMessage) {
          setErrorMessage(err.response.data.errorMessage);
        }
      } else console.log(err);
    }
    return;
  }

  return ready ? (
    <>
      <div>
        <div className="fudheaderdiv">
          <h3 className="fudheader">
                 {user.MA}:
          </h3>
        </div>
        <br />
        {errorMessage && (
          <ErrorMessage
            message={errorMessage}
            clear={() => setErrorMessage(null)}
          />
        )}
        <form>
          <div className="FUD">
            <div className="FUDcolumnFirst">
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">{ma}</div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-courseno"
                    type="number"
                    placeholder=" "
                    defaultValue={courseno}
                    value={fcourseno}
                    onChange={(e) => fsetCourseno(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-birthdate"
                    type="date"
                    placeholder=" "
                    defaultValue={birthdate}
                    value={fbirthdate}
                    onChange={(e) => fsetBirthdate(e.target.value)}
                  />
                </div>
              </div>
            </div>
            <div className="FUDcolumn">
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-firstname"
                    type="text"
                    placeholder=" "
                    defaultValue={firstname}
                    value={ffirstname}
                    onChange={(e) => fsetFirstname(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-lastname"
                    type="text"
                    placeholder=" "
                    defaultValue={lastname}
                    value={flastname}
                    onChange={(e) => fsetLastname(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle">: </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-nickname"
                    type="text"
                    placeholder=""
                    defaultValue={nickname}
                    value={fnickname}
                    onChange={(e) => fsetNickname(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <br />
            </div>
            <div className="FUDcolumn">
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-mainphone"
                    type="text"
                    placeholder=" "
                    defaultValue={mainphone}
                    value={fmainphone}
                    onChange={(e) => fsetMainphone(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle">  : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-emergencyphone"
                    type="text"
                    placeholder="    "
                    defaultValue={emergencyphone}
                    value={femergencyphone}
                    onChange={(e) => fsetEmergencyphone(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle"> " :</div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-email"
                    type="email"
                    placeholder="   ()"
                    defaultValue={email}
                    value={femail}
                    onChange={(e) => fsetEmail(e.target.value)}
                  />
                </div>
              </div>
            </div>
            <div className="FUDcolumn">
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-addresscity"
                    type="text"
                    placeholder=" "
                    defaultValue={addresscity}
                    value={faddresscity}
                    onChange={(e) => fsetAddresscity(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-addressline"
                    type="text"
                    placeholder=" "
                    defaultValue={addressline}
                    value={faddressline}
                    onChange={(e) => fsetAddressline(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle">: </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-rank"
                    type="text"
                    placeholder=""
                    defaultValue={rank}
                    value={frank}
                    onChange={(e) => fsetRank(e.target.value)}
                  />
                </div>
              </div>
            </div>
            <div className="FUDcolumn">
              <div className="fudunit">
                <div className="fudTitle">: </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-unit"
                    type="text"
                    placeholder=""
                    defaultValue={unit}
                    value={funit}
                    onChange={(e) => fsetUnit(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle"> : </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-addressline"
                    type="text"
                    placeholder=" "
                    defaultValue={soogHatsava}
                    value={fsoogHatsava}
                    onChange={(e) => fsetSoogHatsava(e.target.value)}
                  />
                </div>
              </div>
              <br />
              <div className="fudunit">
                <div className="fudTitle">: </div>
                <div className="fudContent">
                  <input
                    className="fudinput"
                    id="form-maslool"
                    type="text"
                    placeholder=""
                    defaultValue={maslool}
                    value={fmaslool}
                    onChange={(e) => fsetMaslool(e.target.value)}
                  />
                </div>
              </div>
            </div>
          </div>
        </form>
        <br />
        <br />
        <div className="fudupdatebuttondiv">
          <button className="fudupdatebutton" onClick={updatefud}>
             
          </button>
        </div>
      </div>
      <br />
      <br />
      <br />
    </>
  ) : (
    <h2>      ...</h2>
  );
}

export default UpdateFUD;
